import { replaceFunctionsWithIds } from "../redux/middleware/function_presever";

function getArrayFromStringIndex(str: string) {
  // Remove all brackets and quotes, then split by period
  return str.replace(/[\[\]'"]/g, "").split(".");
}

export function setFun(value: any, index: any[] | null | string = null) {
  let r = Math.floor(Math.random() * 100);

  let indexParsed =
    typeof index == "string" ? getArrayFromStringIndex(index) : index;
  return {
    ["$$@@@@__upsert_hook_" + r]: {
      value: replaceFunctionsWithIds(value),
      index: indexParsed,
      isFunction: typeof value == "function",
      valueType: "function",
    },
  };
}
